<template>
  <div class="container-mini">
    <common-list
      ref="jjzqProductList"
      class="common-list"
      :post-url="list.postUrl"
      :req-data="list.reqData"
      :data-name="list.dataName"
      @returnData="getPrizeLog($event)"
    >
      <!-- 循环 begin -->
      <div class="goods-main">
        <div v-for="(item,index) in jjzqProductList" :key="index" class="project-list">
          <div class="project-list-c">
            <div class="project-name">{{ item.xmmc }}</div>
            <div class="project-countdown">
              <span class="remaintime">{{ item.djs?item.djs.day:'' }}</span> 天 <span class="remaintime">{{ item.djs?item.djs.hour:'' }}</span> 时 <span class="remaintime">{{ item.djs?item.djs.minute:'' }}</span> 分
            </div>
            <div class="project-detail">
              <ul>
                <li>
                  <div class="fieldname"><em>时间</em>{{ item.fbsj?getTime(item.fbsj):'' }}</div>
                  <div class="fieldname"><em>单位</em>{{ item.yzdwmc }}</div>
                  <div class="fieldname"><em>联系人</em>{{ item.lxr }}</div>
                  <div class="fieldname"><span class="ybj"><van-icon name="volume-o" /></span><em>已报价</em>
                    <span class="num">{{ item.bjsl }}</span>条
                  </div>
                </li>
              </ul>
            </div>
            <div class="project-label">
              <van-tag plain type="primary">{{ item.xmzt }}</van-tag>
            </div>
            <div class="list-btn">
              <ul>
                <li><van-button type="default" plain size="small" @click="wycy(item.xmxh)"><i class="iconfont iconzengjia" /> 我要参与</van-button></li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <!-- 循环 end -->
    </common-list>
  </div>
</template>

<script land="script" scoped>
import { dsjjzqList } from '@/store/module.js'
import commonList from '@/components/list/commonList1.vue'
export default {
  components: {
    commonList
  },
  data() {
    return {
      jjzqProductList: [],
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      }
    }
  },
  computed: {

  },
  created() {
    this.$bus.$on('jjlbList', (reqData) => {
      this.$refs.jjzqProductList.onRefresh(reqData)
    })
    this.init()
  },
  beforeDestroy() {
    this.$bus.$off('jjlbList')
  },
  methods: {
    getTime(item) {
      item = item + ''
      return item.slice(0, 4) + '/' + item.slice(4, 6) + '/' + item.slice(6, 8) + ' ' + item.slice(8, 10) + ':' + item.slice(10, 12)
    },
    getPrizeLog(data) {
      this.jjzqProductList = data
    },
    init() {
      this.list.postUrl = dsjjzqList
      this.list.reqData = {
        pageno: 1,
        pagerow: 8,
        xmmc: this.xmmc
      }
      this.list.dataName = 'list'
      this.list.noDataText = '暂无更多订单'
    },
    wycy(xmxh) {
      this.$store.commit('bid/SET_XMXH', xmxh)
      this.$router.push({ name: 'BidDetail', params: { xmxh: xmxh }})
    }
  }

}
</script>

<style lang="less" scoped>

</style>
