<template>
  <div>
    <!-- header begin -->
    <!-- <div class="nav-bar">
      <div class="nav-bar-left" @click="goBack()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">竞价列表</div>
      <div class="nav-bar-right" @click="showPopup">
        <i class="iconfont iconsousuo" />
      </div>
    </div> -->
    <!-- header end -->
    <headerBar :header-data="headerData" @searchClick="searchAction()" />
    <div class="container-tab">
      <van-sticky class="tabs-sticky">
        <van-tabs v-model="active" swipeable animated @change="change">
          <van-tab v-for="(item ,index) in tabs" :key="index" :title="item">
            <Jjlb v-if="index == 0 " ref="mychild" />
            <Jjjg v-if="index == 1 " ref="mychild" />
          </van-tab>
        </van-tabs>
      </van-sticky>
    </div>
    <searchMeun @goSearchAction="search($event)" @reSet="reSet">
      <van-field v-model="xmmc" label="项目名称" placeholder="请输入项目名称" input-align="right" />
    </searchMeun>
    <!-- 查询条件  begin -->
    <!-- <van-popup v-model="show" position="top" class="search-popup" overlay-class="transparent-overlay">
      <div class="common-main box">
        <div class="spe-input">
          <van-field v-model="xmmc" label="项目名称" placeholder="请输入项目名称" input-align="right" />
          <van-cell title="项目状态" is-link value="请选择项目状态" @click="showcgfsPopup" />
          <van-cell title="发布时间" is-link value="请选择开标-结束时间" @click="showcalendar" />
        </div>
        <div class="search-popup-btn">
          <van-button type="info" block round @click="search()">搜索</van-button>
        </div>
      </div>
    </van-popup> -->
    <!-- 查询条件  end -->

    <!-- 采购方式弹出层 beign -->
    <van-popup
      v-model="show1"
      position="bottom"
      closeable
      round
    >
      <van-picker :columns="columns" />
    </van-popup>
    <!-- 采购方式弹出层 end -->

    <!-- 日期选择 begin -->
    <van-calendar v-model="show2" type="range" color="#1890FF" />
    <!-- 日期选择 begin -->
  </div>
</template>

<script land="script" scoped>
import Jjlb from './list/Jjlb'
import Jjjg from './list/Jjjg'
import searchMeun from '@/components/searchComponents/searchMeun.vue'
import headerBar from '@/components/header/headerBar.vue'
export default {
  name: 'BidList',
  components: {
    Jjlb,
    Jjjg,
    searchMeun,
    headerBar
  },
  data() {
    return {
      headerData: {
        title: '竞价列表',
        leftUrl: 'back',
        leftText: ' ',
        showSearch: true
      },
      pageName: '最新竞价', // 当前tab页名称
      xmmc: '',
      active: '',
      date: '2/12 - 2/14',
      value1: '',
      show: false,
      show1: false,
      show2: false,
      columns: ['竞价中', '二次竞价', '竞价结束'],
      tabs: ['最新竞价', '竞价结果']
    }
  },
  computed: {

  },
  created() {

  },

  methods: {
    reSet() {
      this.xmmc = ''
    },
    change(data, ref) {
      this.xmmc = ''
      this.pageName = ref
    },
    search() {
      this.$bus.$emit('showMeun', false)
      const reqData = {
        pageno: 1,
        pagerow: 8,
        xmmc: this.xmmc
      }
      if (this.pageName === '最新竞价') {
        this.$bus.$emit('jjlbList', reqData)
      }
      if (this.pageName === '竞价结果') {
        this.$bus.$emit('jjjgList', reqData)
      }
      this.show = false
    },
    goBack() {
      this.$router.back()
    },
    showPopup() {
      this.show = true
    },
    showcgfsPopup() {
      this.show1 = true
    },
    showcalendar() {
      this.show2 = true
    },
    formatDate(date) {
      return `${date.getMonth() + 1}/${date.getDate()}`
    },
    onConfirm(date) {
      const [start, end] = date
      this.show2 = false
      this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`
    }
  }

}
</script>

<style lang="less" scoped>
.nav-bar{box-shadow: none;}
</style>
